import { useForm }   from "react-hook-form";
import { useParams } from "react-router-dom";

import { getCurryingFile, isValidArray } from "Helpers";
import { useHandlerMutation }            from "Helpers/Hooks";
import { AppraisalHOCPageFetcher }       from "Components/Appraisal";
import AppraisalSection3                 from "./AppraisalSection3";

const PARAMS =  { select_fields : ["page_three", "files"] };
const DEFAULT_FETCH_DATA = { page_three : {}, files : [] };
const BOUNDARIES = [{
	address    	: "",
	distance   	: "",
	description	: "",
}];

const AppraisalSection3Container = ({ appraise = DEFAULT_FETCH_DATA }) => {

	const { id : appraiseId } = useParams();

	// ------------> RTK-Q <---------------
	const [ editApprasial, { isLoading  : isLoadingMutation } ] = useHandlerMutation({ toastName : "apprasialCRUD" });

	const { page_three, files } = appraise;

	// ------------> hookForm <---------------
	const getFile = getCurryingFile(files);

	const defaultValues = {
		...page_three,
		private_boundarie  : getFile("private_boundarie"),
		land_boundarie     : getFile("land_boundarie"),
		private_boundaries : isValidArray(page_three.private_boundaries) ? page_three.private_boundaries : BOUNDARIES,
		land_boundaries    : isValidArray(page_three.land_boundaries) ? page_three.land_boundaries : BOUNDARIES,
	};

	const hookForm = useForm({ defaultValues });

	const { handleSubmit } = hookForm;

	const deleteFiles = (dataToSubmit) => {
		delete dataToSubmit?.private_boundarie;
		delete dataToSubmit?.land_boundarie;
	};

	const onSubmit =  handleSubmit(async (formData) => {
		const dataToSubmit = { ...formData };
		deleteFiles(dataToSubmit);

		await editApprasial({
			module : `/appraisals/page-three/${appraiseId}`,
			method : "PUT",
			data   : dataToSubmit,
			tags   : ["appraisals", "apprasial_details"],
		});
	});

	return (
		<AppraisalSection3
			delegations={{
				onSubmit,
				hookForm,
				isLoading : isLoadingMutation,
			}}
		/>
	);
};

export default AppraisalHOCPageFetcher(AppraisalSection3Container, PARAMS);
