import {
	Land,
	Tabs,
	HeaderPage,
	LandBoundaries,
	UploadFileModal,
	PrivateBoundaries,
} from "Components/Appraisal";

const tabs = [
	{
		title : "Terreno",
		Tab   : Land,
	},
	{
		title : "Colindancias del Terreno",
		Tab   : LandBoundaries,
	},
	{
		title : "Colindancias Privativas",
		Tab   : PrivateBoundaries,
	},
];

const AppraisalSection3 = ({
	delegations :{
		hookForm,
		onSubmit,
		isLoading,
	},
}) => {

	const { formState : { isDirty } } = hookForm;

	return (
		<>
			<HeaderPage
				currentIndex={[1, 0]}
				section="body"
				isDirty={isDirty}
				onSubmit={onSubmit}
				isSubmitingData={isLoading}
			/>
			<Tabs
				tabs={tabs}
				hookForm={hookForm}
				onSubmit={onSubmit}
				section="2.1 Terreno"
				isSubmitingData={isLoading}
			 />
			<UploadFileModal hookForm={hookForm} />
		</>
	);
};

export default AppraisalSection3;
